	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/04/netter-versuch-mama.jpg\" alt=\"Netter Versuch, Mama\" /></p>\n<p>MamaMaus brought home this picture and said it would be good for PapaScott. File it under McDonald's humor that McDonald's people find funny. If you've never sent a child to a German school, this is obviously a school lunch <em>Pausenbrot</em> in a different wrapping.</p>\n<p>It seems to come from an <a href=\"http://derstandard.at/?url=/?id=2464453\">ad campaign for McDonald's Austria</a>.</p>\n<p>No, we don't do this. We wrap Christopher's <em>Pausenbrot</em> in normal waxed paper like all other parents.</p>","fields":{"title":"Nice Try, Mama","date":"08 Apr 2007","url":"https://papascott.de/2007-04-08-nice-try-mama/"}}},"pathContext":{"slug":"/2007-04-08-nice-try-mama/","prev":{"fields":{"title":"I Got the Bug","date":"07 Apr 2007","slug":"/2007-04-07-i-got-the-bug-2/"}},"next":{"fields":{"title":"Minicruise Photo Set","date":"09 Apr 2007","slug":"/2007-04-09-minicruise-photo-set/"}}}}

